import React from 'react';
import withAuthMethods from '../WithAuthMethods';

class RegisterForm extends React.Component {
  state = {
    name: '',
    lname: '',
    email: '',
    password: '',
  };

  handleChange = (e) => {
    const { name, value } = e.target;
    this.setState({ [name]: value });
  }

  handleSubmit = (e) => {
    e.preventDefault();
    this.props.signUp(this.state);
  }

  render() {
    console.log(this.props);

    return (
      <div style={{width:"1356px",margin:"0 auto"}}>
        <form onSubmit={this.handleSubmit}>
          <div className="form-row align-items-center">
            <div className="col-auto">
              <input name="name" type="text" className="form-control" placeholder="" onChange={this.handleChange} />
            </div>
            <div className="col-auto">
              <input name="lname" type="text" className="form-control" placeholder="" onChange={this.handleChange} />
            </div>
            <div className="col-auto">
              <input name="email" type="email" className="form-control" placeholder="Email" onChange={this.handleChange} />
            </div>
            <div className="col-auto">
              <input name="password" type="password" className="form-control" placeholder="" onChange={this.handleChange} />
            </div>
            <div className="col-auto">
              <button type="submit" className="btn btn-primary"></button>
            </div>
          </div>
        </form>
      </div>
    )
  }
}

export default withAuthMethods(RegisterForm);
